body{
    margin: 0%;
    padding: 0%;
    background-color: #ffffff;
    font-family: 'Cantarell', sans-serif;
}

.container{
    height: 100vh;
    width: 70%;
    margin-right: auto;
    margin-left: 400px;
    float: left;
}

.wrapper{
    height: 100vh;
    width: 1500px;
    margin-right: auto;
    margin-left: auto;  
}

.about-container{
    height: 100%;
    width: 100%;
    .paragraph-positioning{
        position: relative;
        top: 50%;  
        transform: translateY(-50%);
    }
}

.section-positioning{
    height: 100%;
    width: 100%;
    .paragraph-positioning{
        position: relative;
        top: 20%;  
        transform: translateY(-20%);
    }
}

.flex-container{
    height: 80vh;
    width: 100%;
}

.grid-container {
    display: inline-flex;
    flex-flow: column wrap;
    padding: 10px;
    list-style: none;
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
    // background-color: #2196F3;
}

.img-container{
    position: fixed;
    left: 50%;
    top: 25%;
    transform: translateX(-50%);   
    z-index:-1;
    img{
        opacity: 0.8;  
        animation: slide-up 1.2s ease;
    }
}

.img-animation{ 
    display: none;
}

li:hover .img-animation {
    display: block;
}

ol li a{
    text-decoration: none;
    display: inline;
}

/* NAVBAR */
#sidebar{
    position: fixed;
    width: 350px;
    height: 100vh;
    left: 0px;
    background-color: #000000;
    text-align: center;
    .positioning{
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }
    .img-size{
        height: auto;
        max-width: 75%;
        border-radius: 50%;
    }
}

ul {
    text-decoration: none;
    list-style: none;
}

nav li a{
    text-decoration: none;
    font-weight: 800;
    font-size: 1.1em;
    line-height: 2.5em;
    color: #dddbdb;
}

nav li.active a{
    color: #067cca;
}

nav li a:hover{
    color: #067cca;
}

.link-list{
    color: #343a40;
    font-weight: 800;
}

.link-list:hover{
    color: #067cca;
}

.row {
    width: 100%;
    text-align: center;
}

.each-list{
    // background-color: rgba(255, 255, 255, 0.8);
    // border: 1px solid rgba(0, 0, 0, 0.8);
    font-size: 1.6em;
    margin-block-end: 0%;
    padding: 2% 0 2% 0;
    text-align: center;
}

.each-list:hover{
    font-size: 2em;
    animation: shake 0.3s; // Start the shake animation and make the animation last for 0.5 seconds
    animation-iteration-count: infinite; // When the animation is finished, start again
}

ol:hover a{
    opacity: 0.5;
}

ol a:hover{
    opacity: 1;
}

/* NAVIGATION CROSS */
.close {
    position: absolute;
    right: 200px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}

.close:hover {
opacity: 1;
}

.close:before, .close:after {
position: absolute;
left: 15px;
content: ' ';
height: 33px;
width: 2px;
background-color: #333;
}

.close:before {
transform: rotate(45deg);
}

.close:after {
transform: rotate(-45deg);
} 

/* KEYFRAMES */
@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
}  

@keyframes slide-up{
    0% { transform: translateY(200px); }
    100% { transform: translateY(0); }
    from { opacity: 0; }
    to {opacity: 0.8; }
}

